.container {
  width: 800px;
  margin: 0 auto;
}

section {
  border: 1px solid #000;
  height: 95vh;
  margin-top: 5px;
}

.user {
  margin-top: 5px;
  height: 50px;
  border: 1px solid #5f5f5f;
  border-radius: 10px;
}

.row {
  display: flex;
}

.col-1 {
  width: 20%;
  border: 1px solid #5f5f5f;
  border-radius: 10px;
  margin: 2px 0px 5px 5px;
  height: 85.2vh;
}

.userdetails {
  float: right;
}

.user img {
  display: block;
  margin-top: 5px;
}

.user span {
  display: block;
  margin-right: 10px;
}

.col-2 {
  width: 70%;
}

.profile, .mytasks {
  background: transparent;
  border: 1px solid #eeeeee;
  border-radius: 4px;
  width: 80%;
  outline: none;
  cursor: pointer;
  margin-left: 15px;
  padding: 4px;
}

.common {
  background: transparent;
  border: 1px solid #eeeeee;
  border-radius: 4px;
  width: 80%;
  outline: none;
  cursor: pointer;
  background-color: #f0f0f0;
  margin: 5px;
}

.profile {
  background-color: #FE7A40;
}

.mytasks {
  background-color: #f0f0f0;
}

.profileinfo, .notificationinfo, .taskinfo {
  width: 400px;
  height: 400px;
  margin: 30px auto;
  border: 1px solid #5f5f5f;
}

.profileinfo span {
  margin: 40px 34px 0;
  padding: 2px 20px;
  background-color: #eeeeee;
  width: 15%;
  display: inline-block;
  text-align: center;
  border: 1px solid #f0f0f0;
  border-radius: 5px;
}

.profileinfo input {
  width: 35%;
  margin: 0px 40px;
  border-radius: 5px;
  outline: none;
  height: 22px;
  border: 1px solid #000;
}

.taskinfo span {
  margin: 25px 20px 0;
  padding: 2px 20px;
  background-color: #eeeeee;
  width: 30%;
  display: inline-block;
  text-align: center;
  border: 1px solid #f0f0f0;
  border-radius: 5px;
}

.taskinfo input {
  width: 35%;
  border-radius: 5px;
  outline: none;
  height: 22px;
  border: 1px solid #000;
}

/* .notificationinfo input {
  width: 35%;
  border-radius: 5px;
  outline: none;
  height: 22px;
  border: 1px solid #000;
} */

.notificationinfo .ncol-1 span {
  margin: 40px 25px 0;
  padding: 2px 5px;
  background-color: #eeeeee;
  border: 1px solid #f0f0f0;
  display: block;
  text-align: center;
}

.row {
  display: flex;
}

.notificationinfo .ncol-1 {
  width: 60%;
}

.notificationinfo .ncol-2 {
  width: 30%;
}

.title {
  display: inline-block;
  padding: 2px 10px;
  background-color: #eeeeee;
  border-radius: 5px;
  margin: 3% 0 0% 40%;
}

.setting {
  background-color: turquoise;
}

.profilelist {
  display: none;
}

.mytaskslist {
  display: none;
}

ul {
  list-style: none;
  margin: 35px 22px 0px 00px;
}

li {
  margin: 20px 35px 0px 0px;
  display: inline-flex;
  padding: 3px 10px;
  color: #fff;
  border-radius: 5px;
  float: right;
}

.cancelbtn {
  background-color: #dc143c;
  cursor: pointer;
}

.submitbtn {
  background-color: green;
  cursor: pointer;
}

.validate {
  background-color: #3ca1da;
}

select {
  width: 100px;
  border: 1px solid #eeeeee;
  height: 25px;
  background-color: #f0f0f0;
  border-radius: 5px;
}

textarea {
  resize: none;
  height: 40px;
}

.checkbox-btn {
  width: 100px;
  height: 25px;
  border: 1px solid #000;
  border-radius: 10px;
  margin-top: 40px;
  margin-left: 20px;
  color: #fff;
  font-weight: bold;
  overflow: hidden;
  position: relative;
}

.ON {
  cursor: pointer;
  width: 80%;
  height: 100%;
  background-color: #3ca1da;
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 5px;
}

.OFF {
  cursor: pointer;
  width: 80%;
  background-color: #ff0000;
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
  display: none;
  border-radius: 5px;
}

.ON span {
  display: block;
  margin-left: 20px;
}

.OFF span {
  display: block;
  text-align: right;
  margin-right: 20px;
}

.msg {
  text-align: center;
  display: block;
  color: green;
}